  import React, { useState } from "react";
  import { Link, useLocation } from "react-router-dom";
  import SVG from "react-inlinesvg";
  import { toAbsoluteUrl } from "../../../utils";
  import { Button, Nav, OverlayTrigger, Tooltip } from "react-bootstrap";
  import FeedsList from "../feedsList";
  import DateRangePicker from "react-bootstrap-daterangepicker";
  import { shallowEqual, useDispatch } from "react-redux"; 
  import { useSelector } from "react-redux";
  import queryString from "query-string";
  import moment from "moment";
  import { activeTab, refreshButton, updateFilterDate, updateSelectedFeed } from "../../../../../../../actions/analyticsAction";
  import { useEffect } from "react";
import { FontAwesomeIcon } from "../../../../../../CommonComponents/AssetsHelper";

  const Subheader = (props) => {
    const location = useLocation();
    const dispatch = useDispatch();

    const { analyticwall, analyticsStatus, pageRefresh, analytics_status_loader, getactiveTab, analyticsRecords, dateFilter } = useSelector(
      ({ analyticwallD, analyticsStatusD, analyticsRecords }) => ({
        analyticwall: analyticwallD,
        analyticsStatus: analyticsStatusD,
        pageRefresh: analyticsRecords.page_refresh,
        analytics_status_loader : analyticsRecords.analytics_status_loader,
        dateFilter : analyticsRecords.dateFilter,
        getactiveTab : analyticsRecords.activeTab,
        analyticsRecords : analyticsRecords
      }),
      shallowEqual
    );

    const trialAccessAPI =  !analyticwall.selectedFeed.length ? true : analyticsStatus.trialAccessAPI

 
    const filterDate =
      analyticwall && Object.keys(analyticwall).length > 0
        ? analyticwall.dateFilter
        : null;
    let startdateupdate =
      filterDate && Object.keys(filterDate).length > 0 && filterDate.startDate
        ? filterDate.startDate
        : moment().subtract(29, "days").format("DD-MM-YYYY");
    let enddateupdate =
      filterDate && Object.keys(filterDate).length > 0 && filterDate.endDate
        ? filterDate.endDate
        : moment().format("DD-MM-YYYY");
    const parsed = queryString.parse(location.search);
    if (parsed.startdate) {
      startdateupdate = moment.unix(parsed.startdate).format("DD-MM-YYYY");
    }
    if (parsed.enddate) {
      enddateupdate = moment.unix(parsed.enddate).format("DD-MM-YYYY");
    }

    const [startDate, setStartDate] = useState(startdateupdate);
    const [endDate, setEndDate] = useState(enddateupdate);
    const [maxDate, setMaxDate] = useState(
      moment().endOf("month").format("DD-MM-YYYY")
    );

     const handleOnApply = (event, picker) => {

        dispatch(  
          updateSelectedFeed(analyticsRecords.selected_feed_data,trialAccessAPI,
            { startDate: picker.startDate, endDate: picker.endDate }
          )
        )

        setStartDate(picker.startDate.format("DD-MM-YYYY"));
        setEndDate(picker.endDate.format("DD-MM-YYYY"));
       // dispatch(updateFilterDate({startDate: picker.startDate, endDate: picker.endDate }));
     }

    // const callbackFeed=()=>{  
    //   dispatch(
    //     updateSelectedFeed(analyticwall.selectedFeed,trialAccessAPI,
    //       { startDate: dateFilter.startDate, endDate: dateFilter.endDate }
    //     )
    //   );
    //   setStartDate(dateFilter.startDate.format("DD-MM-YYYY"));
    //   setEndDate(dateFilter.endDate.format("DD-MM-YYYY"));
    // }

    useEffect(() => {
       dispatch(activeTab(location.pathname))
    }, []);

  
    const onClickRefreshButton = event => {
       dispatch(refreshButton(trialAccessAPI))
    }

    return (
      <> 
      <div id="sw_subheader" className="analytics_subheader bg-white box-shadow">
        <div className="container-fluid f-between flex-wrap position-relative ps-0">
          <div className="d-flex align-items-center">
            <Link className="btn me-3 border-end border-0 py-3 rounded-0" to={`/wall/index/${localStorage.getItem('wallId')}`}>
                <FontAwesomeIcon type={'regular'} icon={'chevron-left'}/>
            </Link>

            <Nav className="sw-anlts-nav-tabs" defaultActiveKey={`/analytic/${localStorage.getItem('wallId')}`} as={'ul'}>
              <Nav.Item className="me-2" as="li">
                <Link to={`/analytic/${localStorage.getItem('wallId')}`} className={`nav-link ${location.pathname.includes(localStorage.getItem('wallId')) ? `active` : ``}`}>Content</Link>
              </Nav.Item>
              <Nav.Item as="li">
                <Link to={"/analytic/activityHub/widget"} className={`nav-link ${location.pathname.includes("activityHub")? `active`: ``}`}>Activity Hub</Link>
              </Nav.Item>
            </Nav>
            {/* <ul className="nav sw-anlts-nav-tabs"> 
              <li className="me-3">   
                <Link to={`/analytic/${localStorage.getItem('wallId')}`} className={`nav-link ${location.pathname.includes(localStorage.getItem('wallId')) ? `active` : ``}`}>
                  Content 
                </Link>
              </li>
              <li className="me-3">
                <Link to={"/analytic/activityHub/widget"} className={`nav-link ${location.pathname.includes("activityHub")? `active`: ``}`}>
                  Activity Hub
                </Link>
              </li>
            </ul> */}
          </div>
          <div className="d-flex justify-content-end flex-grow-1">
              <div className="option-list active">
                  <div className="d-flex">
                    <div className="ms-2">
                        <Button variant="outline-secondary" onClick={()=>onClickRefreshButton()} className="h-32px w-32px f-center">
                          <OverlayTrigger placement="bottom" overlay={<Tooltip className="my-custom-tooltip">Reffresh</Tooltip>}>
                              <span className={`d-flex${(pageRefresh && pageRefresh) ? ` fa fa-spin` : ``}`}>
                                <FontAwesomeIcon type={'solid'} icon={'arrows-rotate'} iconClass={'fs-6'}/>
                              </span> 
                          </OverlayTrigger> 
                        </Button>
                    </div>
                  { !props.activeTab && ![3].includes(getactiveTab) ? <div className="ms-2">
                    { analytics_status_loader ? <FeedsList /> : null }
                    </div> : null }
                    
                  { ![3].includes(getactiveTab) ?  
                    <div className="ms-2">
                      <DateRangePicker onApply={handleOnApply} startDate={startDate} endDate={endDate}
                        initialSettings={{
                          locale: { format: "DD-MM-YYYY", separator: "-" },
                          maxDate: maxDate,
                          showDropdowns: true,
                          startDate: startDate,
                          endDate: endDate,
                          ranges: {
                            Today: [
                              moment().format("DD-MM-YYYY"),
                              moment().format("DD-MM-YYYY"),
                            ],
                            Yesterday: [
                              moment().subtract(1, "days").format("DD-MM-YYYY"),
                              moment().subtract(1, "days").format("DD-MM-YYYY"),
                            ],
                            "Last 7 Days": [
                              moment().subtract(6, "days").format("DD-MM-YYYY"),
                              moment().format("DD-MM-YYYY"),
                            ],
                            "Last 30 Days": [
                              moment().subtract(29, "days").format("DD-MM-YYYY"),
                              moment().format("DD-MM-YYYY"),
                            ],
                            "This Month": [
                              moment().startOf("month").format("DD-MM-YYYY"),
                              moment().endOf("month").format("DD-MM-YYYY"),
                            ],
                            "Last Month": [
                              moment()
                                .subtract(1, "month")
                                .startOf("month")
                                .format("DD-MM-YYYY"),
                              moment()
                                .subtract(1, "month")
                                .endOf("month")
                                .format("DD-MM-YYYY"),
                            ],
                          },
                        }}
                        // onCallback={handleCallback}
                      >
                        <Button variant="outline-secondary" className="h-32px w-32px f-center" id="reportrange">
                          <OverlayTrigger placement="bottom" overlay={<Tooltip className="my-custom-tooltip">Date Filter</Tooltip>}>
                            <FontAwesomeIcon type={'regular'} icon={'calendar-range'} iconClass={'fs-6'} />
                          </OverlayTrigger>
                        </Button>
                      </DateRangePicker>
                    </div> : null }
                  </div>
              </div>

              <div className="more-option">
                  <Button variant="outline-secondary" className="h-32px w-32px f-center">
                    <FontAwesomeIcon type={'solid'} icon={'ellipsis-vertical'} iconClass={'fs-6'}/>
                  </Button>
              </div>
            </div>
        </div>
      </div>

      
        {/* <div  className="d-flex flex-column-fluid main_content">
            <div className="py-5 w-100">
                <div className="container-fluid px-xl-5">
                      <div className="row">
                        <div className="col-lg-12 col-xxl-12">
                            <div className="alert custom-alert">
                                <span>This is a demo data. You cannot see live data because you haven't added a post yet. Please add posts to see real time analytics.</span>
                                  <a className="free-trial-upgrade cursor-pointer" onClick={()=>dispatch(togglePlanSection(true))}>
                                    Upgrade Now
                                </a> 
                            </div>
                        </div>
                    </div>  
                </div>
            </div>
        </div>           */}
      </>
    );
  };

  export default Subheader;
