import React, { Component, memo } from "react";
import { connect } from "react-redux";
import ReactHtmlParser from 'react-html-parser';
import ReactMultiSelectCheckboxes from 'react-multiselect-checkboxes';
import { feedsNetworksPrefix, networkIcon } from '../../../../../../utils';
import { activeTab, swithcWall, updateSelectedFeed } from "../../../../../../actions/analyticsAction";
import { useLocation } from "react-router-dom";

const FeedsList=(props)=>{
  const location = useLocation()
   return <FL location={location} {...props}></FL>
}

class FL extends Component {
  state = { feed: [], wallID: null, getwallID: null, updateFeedList: [], refreshing: true, spece: `-` }
  componentDidMount() {
    const { analyticwall, analyticsStatus } = this.props
    const { spece } = this.state
    let updateFeedList = []; let wallID = 0;
    if (analyticwall.selectedWallFeed && Object.keys(analyticwall.selectedWallFeed).length > 0) {
      if (analyticwall.selectedWallFeed && analyticwall?.selectedWallFeed?.feeds?.length > 0) {
        wallID = analyticwall?.selectedWallFeed?.wall?.id;
        analyticwall?.selectedWallFeed?.feeds.map((item) => {
          updateFeedList.push({ value: item.id, label: ReactHtmlParser(`${item.Network.icon ? networkIcon(item.Network.icon) : ""}  ${feedsNetworksPrefix(item.filterId)}  ${item.name}  ${item.filterIdName ? spece : ''} ${item.filterIdName ? item.filterIdName : ''}`) })
        })
      }
    }


    if(analyticwall?.selectedWallFeed?.wall.id!=localStorage.getItem('wallId')){
        this.props.swithcWall()
    }

    this.setState({ getwallID : analyticwall?.selectedWallFeed?.wall?.id})
    this.setState({ updateFeedList, wallID, feed: this.state.wallID != wallID ? updateFeedList : this.state.feed }, () => this.props.updateSelectedFeed(this.state.feed, analyticsStatus.trialAccessAPI))
  }

  changeFeedsFilters = (event) => { 
    const { analyticsStatus } = this.props;
     if(event.length>0) this.setState({ feed: event }, () => this.props.updateSelectedFeed(this.state.feed, analyticsStatus.trialAccessAPI))
  }

  componentWillReceiveProps(nextProps) {
    const { analyticwall, wallRefresh } = nextProps
    const { refreshing, spece } = this.state;
    let updateFeedList = []; let wallID = 0;
    if (analyticwall.selectedWallFeed && Object.keys(analyticwall.selectedWallFeed).length > 0) {
      if (analyticwall.selectedWallFeed && analyticwall?.selectedWallFeed?.feeds?.length > 0) {
        wallID = analyticwall?.selectedWallFeed?.wall?.id;
        analyticwall?.selectedWallFeed?.feeds?.map((item) => {
          updateFeedList?.push({ value: item.id, label: ReactHtmlParser(`${item.Network.icon ? networkIcon(item.Network.icon) : ""}  ${feedsNetworksPrefix(item.filterId)}  ${item.name}  ${item.filterIdName ? spece : ''} ${item.filterIdName ? item.filterIdName : ''}`) })
        })

      }
    }
    if (updateFeedList != this.state.updateFeedList && wallRefresh && wallRefresh.refreshing && refreshing) {
      this.setState({ updateFeedList, wallID, feed: updateFeedList, refreshing: false })
    }
    else {
      this.setState({ updateFeedList, wallID, feed: this.state.wallID != wallID ? updateFeedList : this.state.feed }, () => setTimeout(() => this.setState({ refreshing: true }), 2000))
    }
  }

  render() {

    const { updateFeedList, feed } = this.state;
    return <div className="w-auto react-search-feed filter_btn_wrap">
      <ReactMultiSelectCheckboxes
        classNamePrefix={`check-feed`}
        value={feed}
        onChange={this.changeFeedsFilters}
        options={updateFeedList}
      />
    </div>
  }
}

 
const mapStateToProps = (state) => {  
  return {
    analyticwall: state.analyticwall,
    analyticsStatus: state.analyticsStatusD,
    wallRefresh: state.wallRefresh
  }
}
const mapDispatchToProps = dispatch => {
  return {
    activeTab: (data) => dispatch(activeTab(data)),
    updateSelectedFeed: (data, isTrial) => dispatch(updateSelectedFeed(data, isTrial)),
    swithcWall: () => dispatch(swithcWall()),
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(memo(FeedsList));